"use client"
import AnimatedLogo from "@/app/components/AnimatedLogo";
import {motion, useInView} from "framer-motion"
import {useEffect, useRef, useState} from "react";
import {usePathname} from "next/navigation";
export default function Home() {
    const [yOffset, setYOffset] = useState("46vh");
    const pathname = usePathname();
    const isRoot = pathname === "/";

    useEffect(() => {
        const updateYOffset = () => {
            if (window.innerWidth < 768) {
                setYOffset("37vh"); // Adjust for mobile
            } else {
                setYOffset("46vh"); // Default for larger screens
            }
        };

        updateYOffset(); // Set initial value
        window.addEventListener("resize", updateYOffset);

        return () => window.removeEventListener("resize", updateYOffset);
    }, []);

    const containerRef = useRef<HTMLDivElement>(null);
    const isInView = useInView(containerRef, { once: true });
    const [textHeight, setTextHeight] = useState(0);

    useEffect(() => {
        if (containerRef.current) {
            setTextHeight(containerRef.current.clientHeight);
        }
    }, []);

  return (
      <div className="h-screen w-screen overflow-y-auto snap-y snap-mandatory scroll-smooth">
          <div className="h-screen min-h-screen flex flex-col items-center justify-center snap-always snap-start">
              <motion.div
                  initial={!isRoot ? {scale: 0.4, y: yOffset} : {scale: 1, y: 0}}
                  animate={!isRoot ? {scale: 0.4, y: yOffset} : {scale: 1, y: 0}}
                  transition={{duration: 1.2, ease: "easeInOut"}}
              >
                  <div className="flex items-center justify-center scale-50 m:scale-50 lg:scale-100 cursor-pointer">
                      <AnimatedLogo/>
                  </div>
              </motion.div>
          </div>
          <div className="h-screen bg-[#EAEAEC] snap-always snap-start text-[#0A0A0A]">
              <div className="flex flex-col w-screen h-screen px-20 pt-16">

                  <div className="relative w-full flex justify-center overflow-hidden">
                      {/* Dynamic "Window" that adapts to text size */}
                      <div className="relative overflow-hidden flex items-center" style={{height: textHeight}}>
                          <motion.h1
                              ref={containerRef}
                              className="text-[4vw] font-bold text-[#EAEAEC] leading-none"
                              initial={{y: "100%"}} // Start below
                              animate={isInView ? {y: "0%"} : {}} // Move up when in view
                              transition={{duration: 1.2, ease: "easeOut"}}
                          >
                              test
                          </motion.h1>
                      </div>
                  </div>
                  <div className="relative w-full overflow-hidden h-[100px] bg-amber-200">
                      <div className="relative overflow-hidden h-[70px] bg-amber-950">
                          <motion.h1
                              initial={{y: "115%"}} // Starts hidden below
                              whileInView={{y: "0%"}} // Moves into view
                              transition={{duration: 1.2, ease: "easeOut", delay: 0.5}}
                              viewport={{once: true}}
                              className="text-[4vw] extra-bold-text"
                          >
                              YOU DO BUSINESS,
                          </motion.h1>
                      </div>
                  </div>
                  <motion.h1 className="text-[4vw] extra-bold-text"
                             whileInView={{y: 0}}
                             initial={{y: "100%"}}
                             transition={{duration: 2, ease: "easeInOut", delay: 0.5}}>
                      YOU DO BUSINESS,
                  </motion.h1>
                  <h1 className="text-[4vw] extra-bold-text justify-end">WE'LL
                      DO <span className="funky-text text-[#CEABC1]">Software</span></h1>
                  <p className="text-[#0A0A0A] w-1/2 ml-auto pt-20">DiSy is a digital development and
                      consultation firm
                      focused on aiding small to medium-sized businesses. Our dedicated team offers end-to-end services,
                      including custom web and software development, consultation, web mastering, maintenance, and
                      design tailored to your business needs and ethos. Whether you want a dedicated team to take care
                      of all your digital needs or simply need expert advice on how to go about it on your own, we've
                      got you covered. Book your free, no-strings-attached consultation today and let us help you take
                      your business to the next level.
                  </p>
              </div>

          </div>
      </div>

  );
}